const fs = require("fs");

const packageJsonFile = fs.readFileSync("./package.json", "utf-8");
const packageJson = JSON.parse(packageJsonFile);

const versionFileContent = `export const versionInfo = { algae: { name: "${packageJson.name}", version: "${packageJson.version}" } };`;

const versionInfoFilePath = "./src/versionInfo.ts";
fs.statSync(versionInfoFilePath, { throwIfNoEntry: true });
fs.writeFileSync(versionInfoFilePath, versionFileContent, "utf-8");

const startIndex = versionFileContent.indexOf("{");
console.log("version file created: ", versionFileContent.substring(startIndex));